@import "reset.scss";
@import "vars.scss";


/*Block*/
@import "./blocks/header.scss";
@import "./blocks/home.scss";
@import "./blocks/destination.scss";
@import "./blocks/crew.scss";
@import "./blocks/technology.scss";
@import "./blocks/notfound.scss";


.loader{
    text-align:center;
    display:flex;
    align-items:center;
    justify-content:center;
    width: 100%;
    height: 100vh;
}

::-webkit-scrollbar { /* chrome based */
    width: 0px;  /* ширина scrollbar'a */
    background: transparent;  /* опционально */
}

html{
    font-size: 18px;
    -ms-overflow-style: none;  /* IE 10+ */
    scrollbar-width: none; /* Firefox */

    @media (max-width:768px) {
        font-size: 16px;
    }

    @media (max-width:600px) {
        font-size: 14px;
    }
}

.container{
    padding: 0 9rem;
    max-width: 1440px;
    margin: 0 auto;


    @media (max-width:1110px) {
        padding: 0 5rem;
    }

    @media (max-width:600px) {
        padding: 0 1.5rem;
    }
}



body{
    font-weight: 400;
    font-family: 'Barlow Condensed', sans-serif;
    background-color: $blue;
    color: #fff;
}





/*Title style*/
h1{
    font-weight: 400;
    font-size: 8.3rem;
    font-family: 'Bellefair', serif;
    text-transform:uppercase;


    @media(max-width:600px){
        font-size: 6rem;
    }
}
h2{
    font-size: 5.5rem;
    font-weight: 400;
    font-family: 'Bellefair', serif;
    text-transform:uppercase;
}
h3{
    font-size: 3.1rem;
    font-weight: 400;
    font-family: 'Bellefair', serif;
    text-transform:uppercase;
}
h4{
    font-size: 1.7rem;
    font-weight: 400;
    font-family: 'Bellefair', serif;
    text-transform:uppercase;
}
h5{
    font-weight: 400;
    font-family: 'Barlow Condensed', sans-serif;
    text-transform:uppercase;
    letter-spacing: 0.26rem;
    
}

.page__title{
    font-size: 1.8rem;
    font-family: 'Barlow Condensed';
    letter-spacing: 0.26rem;
    span{
        padding-right: 1.44rem;
        opacity: 0.25;
        font-weight: 700;
    }


    @media (max-width:600px) {
        text-align: center;
        font-size: 1.6rem;
    }
}

.error{
    color: $purple;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align:center;
    width:100%;
    height: 400px;
    margin:auto auto;
    font-size:2rem;
}